import Vue from 'vue'
import Router from 'vue-router'
import TimKiemHoSo from '@/components/timkiem/timkiemhoso'
import TimKiemVanBan from '@/components/timkiem/timkiemvanban'
import TimKiemHoSoNangCao from '@/components/timkiem/timkiemhosonangcao'
import TimKiemVanBanNangCao from '@/components/timkiem/timkiemvanbannangcao'
import TaoYeuCauKhaiThac from '@/components/timkiem/taoyeucaukhaithac'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: TimKiemHoSo
    },
    {
      path: '/timkiemvanban',
      component: TimKiemVanBan
    },
    {
      path: '/timkiemhosonangcao',
      component: TimKiemHoSoNangCao
    },
    {
      path: '/timkiemvanbannangcao',
      component: TimKiemVanBanNangCao
    },
    {
      path: '/taoyeucaukhaithac',
      component: TaoYeuCauKhaiThac
    }
  ]
})



